.card {
  position: relative;
  background-color: #fff;
  width: 600px;
  padding: 20px;
  max-width: 85vw;
  height: 50vh;
  box-shadow: 0 18px 53px 0 rgba(0, 0, 0, 0.3);
  background-position: top;
  background-size: cover;
  border-radius: 20px;
}
.tinderCards__cardContainer {
  display: flex;
  justify-content: center;
  margin-top: 10vh;
}
.swipe {
  position: absolute;
}
.cardContent {
  width: 100%;
  height: 100%;
}
.card h3 {
  position: absolute;
  bottom: 0;
  color: white;
  margin: 10px;
}
